import React from "react"
import MicrosoftLogin from "react-microsoft-login";

//Material UI Components
import Grid from '@material-ui/core/Grid';

//Hooks
import useHooks from './hooks/use-hooks.js';

//Project Components
import MyCalcs from '../my-calcs';
import MyBIMTracker from '../my-bim-tracker';
import BMWeather from "../bm-weather";
import BMNews from '../bm-news';

//Styles
import useStyles from './main-hub-styles';

export default function MainHub() {
    const {
        handleNotification,
        loggedIn,
        authHandler,
        userData
    } = useHooks();
    const classes = useStyles();
    return (
        <div>
        {!loggedIn && <MicrosoftLogin clientId="2b4cf606-ac2c-4532-a7b0-483d7f2427cc" authCallback={authHandler} className={classes.loginScreen} />}
        {loggedIn && 
        <div>
            <h1>Arup Hub</h1>
            <Grid container direction="row">
                <Grid item className={classes.calcGrid}>
                    <MyCalcs userData={userData} />
                </Grid>
                <Grid item className={classes.weatherGrid}>
                    <BMWeather />
                </Grid>
            </Grid>
            
            <MyBIMTracker />
            {/* <BMNews /> */}
        </div>
        }
        </div>
    );
}