import { useState, useEffect } from "react";
import { format, isThisSecond, isEqual } from "date-fns";

export default () => {
  const [sendNotification, setSendNotification] = useState(false);
  const [loggedIn, setLoggedIn] = useState(false);
  const [userData, setUserData] = useState([]);

  const authHandler = (err, data) => {
    console.log(err, data);
    if (data) {
      setUserData(data);
      setLoggedIn(true);
    }
  };

  const handleNotification = () => {
    Notification.requestPermission().then(() => {
      var notification = new Notification(
        "You have 4 new calculations to complete, click to view"
      );
      notification.onclick = function (event) {
        event.preventDefault();
        window.open(
          "https://bmtools.arup.com/calcs/mechanical/project/demo",
          "_blank"
        );
      };
    });
  };

  useEffect(() => {
    if (sendNotification) {
      const timedNotification = () => {
        console.log("timed notification");
      };
      timedNotification();
      handleNotification();
      setSendNotification(false);
    }
  }, [sendNotification]);

  return {
    handleNotification,
    loggedIn,
    authHandler,
    userData,
  };
};
