import React from "react"

//Hooks
import useHooks from './hooks/use-hooks.js';

//Material UI Components
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';


//Styles
import useStyles from './my-bim-tracker-styles.js';

export default function MyBIMTracker() {
    const {
        myTrackers
    } = useHooks();
    const classes = useStyles();
    return (
        <div>
            <Accordion className={classes.accordion}>
                <AccordionSummary
                    // expandIcon={<ExpandMoreIcon />}
                >
                    <Typography>My BIM Tracker</Typography>
                </AccordionSummary>
                <AccordionDetails>
                <TableContainer component={Paper} className={classes.table}>
                <Table stickyHeader size="small" >
                    <TableHead>
                        <TableCell>Project Name</TableCell>
                        <TableCell>BIM Lead</TableCell>
                        <TableCell>BIM Checker</TableCell>
                        <TableCell>BIM Approver</TableCell>
                    </TableHead>
                    <TableBody>
                        {myTrackers.map((tracker) => (
                         <TableRow key={tracker.ProjectCode}>
                             <TableCell>{tracker.JobNameShort}</TableCell>
                             <TableCell>{tracker.bimLead}</TableCell>
                             <TableCell>{tracker.bimChecker}</TableCell>
                             <TableCell>{tracker.bimApprover}</TableCell>
                         </TableRow>
                    ))}
                    </TableBody>
                </Table>
            </TableContainer>
                </AccordionDetails>
            </Accordion>


            
        </div>
    );
}