import { useState, useEffect } from "react";
const axios = require("axios");

export default () => {
  const [myTrackers, setMyTrackers] = useState([]);

  const getMyTrackers = () => {
    axios
      .get(
        `https://bmtools.arup.com/bimtracker/api/pwadata/colin.magner@arup.com`
      )
      .then(function (response) {
        console.log("trackers", response.data);
        setMyTrackers(response.data);
      });
  };

  useEffect(() => {
    getMyTrackers();
  }, []);

  return {
    myTrackers,
  };
};
