import { useState, useEffect } from "react";
const axios = require("axios");

export default (userData) => {
  const [myCalcs, setMyCalcs] = useState([]);
  const [newCalcs, setNewCalcs] = useState([]);
  const [notificationSent, setNotificationSent] = useState(false);

  const getMyCalcs = () => {
    axios
      .get(
        `https://bmtools.arup.com/calcs/api/mechanical/pwadata/${userData.account.userName}`
      )
      .then(function (response) {
        setMyCalcs(response.data.calcs);
      });
  };

  const getNewCalcs = () => {
    axios
      .get(
        `https://bmtools.arup.com/calcs/api/mechanical/pwadata/${userData.account.userName}`
      )
      .then(function (response) {
        setNewCalcs(response.data.calcs);
      });
  };

  useEffect(() => {
    getMyCalcs();
    setNewCalcs(myCalcs);
  }, []);

  useEffect(() => {
    const checkForCalcs = setInterval(() => {
      getNewCalcs();
      return () => clearInterval(checkForCalcs);
    }, 60000);
  }, []);

  useEffect(() => {
    if (newCalcs.length > myCalcs.length) {
      //Could link to the specific project
      Notification.requestPermission().then(() => {
        var notification = new Notification(
          "You have a new Calculation to complete"
        );
        notification.onclick = function (event) {
          event.preventDefault();
          window.open(
            "https://bmtools.arup.com/calcs/mechanical/mycalcs",
            "_blank"
          );
        };
      });
    } else if (newCalcs.length < myCalcs.length) {
      //Could display how many more to go
      Notification.requestPermission().then(() => {
        new Notification(
          `Well done! You've completed a Calculation, only ${newCalcs.length} more to go.`
        );
      });
    }
    console.log(newCalcs, myCalcs);
    setNotificationSent(true);
  }, [newCalcs]);

  useEffect(() => {
    if (notificationSent === true) {
      getMyCalcs();
      setNotificationSent(false);
    }
    console.log(myCalcs, newCalcs);
  }, [notificationSent]);

  const completeCalc = (calc) => {
    window.open(
      `https://bmtools.arup.com/calcs/mechanical/project/${calc.ProjectCode}`,
      "_blank"
    );
  };

  return {
    myCalcs,
    completeCalc,
  };
};
